body {
    background-color: #010204;; 
}

.cocktail {
    width: 100%;
    margin-top: 100px;
    background-color: #010204;
    padding: 0;
    display: flex;
    justify-content: center;
    margin-top: 60px; 
}

.container{
    padding: 4%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 14px;
    width: 290px;
    background-color: #191a1c;
}

.cocktail_img {
    width: 200px;
    border-radius: 50%;
    border: 2px solid #fff;
    padding: 0.5%;
    margin-top: 5%;
    -webkit-filter: drop-shadow(3px 3px 4px #4444dd);
            filter: drop-shadow(3px 3px 4px #4444dd);
}

.cocktail_name {
    padding: 10px;
    font-family: 'Titillium Web', sans-serif;
    font-size: 26px;
    text-transform: uppercase;
    font-weight: 700;
    font-style: italic;
    text-align: center;
    text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
    animation: glow 1.5s ease-in-out infinite alternate;
}

 @keyframes glow {
  from {
    text-shadow:
    0 0 3px #fff,
    0 0 3px #fff,
    0 0 5px #e60073,
    0 0 5px #e60073,
    0 0 6px #e60073,
    0 0 7px #e60073,
    0 0 7px #e60073;
  }
  to {
    text-shadow:
    0 0 5px #fff,
    0 0 6px #ff4da6,
    0 0 7px #ff4da6,
    0 0 7px #ff4da6,
    0 0 8px #ff4da6,
    0 0 9px #ff4da6,
    0 0 9px #ff4da6;
  }
}

.cocktail_recipe {
    display: flex;
    justify-content: space-around;
    /* background-color: #686d76; */
}

.cocktail_recipe .ingredients {
    width: 140px;
}

.cocktail_recipe .measurements {
    width: 140px;
}

.ingredients, .measurements {
    list-style: none;
}

ul {
    padding: 0;
}

li {
    padding: 3px 0 3px 0;
    font-size: 14px;
    font-weight: 300;
    font-family: 'Titillium Web', sans-serif;
    letter-spacing: 0.5px;
    color:white;
}

.measurements li {
    display: flex;
    justify-content: flex-end;
}


hr {
    border: 0.5px solid white;
}

.cocktail_glass {
    text-align: right;
    margin: 5% 0 5% 0;
    color:white;
    font-family: 'Mukta', sans-serif;
    font-style: italic;
    font-size: 15px;
    font-weight: 200;
    /* text-transform: uppercase; */
    /* border-bottom: 2px solid white; */
}

.cocktail_instruction {
    width: 290px;
    font-family: 'Titillium Web', sans-serif;
    color:white;
    font-size: 14px;
    margin: 0;
    text-align: center;
    text-align: justify; 
    text-justify: inter-word;
}

.search_div {
    margin-top: 104px;
    display: flex;
    justify-content: center;
}

.search_field {
    width: 240px;
    height: 50px;
    color: black;
    background-color: white;
    font-size: 16px;
    border: 0px solid;
    border-radius: 0;
}

.search_field:focus {
    outline: none;
}
