.home_container {
    max-width: 100%;
    margin-top: 24%;
}

.home_container .main_img {
    width: 100%;
    object-fit: cover;
}
.about_container {
    width: 100%;
    margin: 200px 0 0 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
}

.about_container p {
    margin: 1%;
    color: white;
    font-size: 13px;
    font-weight: 300;
    font-family: 'Titillium Web', sans-serif;
    letter-spacing: 0.2px;
}
.nav_container {
    position: fixed;
    top: 0;
    left: 0;
    background-color: #010204;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 70px;
    margin-top: 0;
    z-index: 10;
}

.nav_menus {
    font-family: 'Londrina Outline', cursive;
    font-size: 34px;
    font-weight: 600;
    color: #ff4da6;
    cursor: pointer;
    text-decoration: none;
    text-transform: uppercase;
    margin: 10px;
  text-shadow:
    0 0 4px #0e11b9,
    0 0 10px #e60073,
    0 0 20px #0e11b9
}

.nav_menus:hover {
    color: white;
    
}

.nav_menus:active {
    color: white;
}

.nav_menus:focus {
    border-bottom: 2px solid white;
    padding-bottom: 36px;

}
.scroll-to-top { 
    width: 100%;
    display: flex;
    justify-content: flex-end;

}
.upButton{ 
    position: fixed;
    bottom: 0.4rem;
    right: 0.4rem;
    cursor: pointer;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: 1px solid white;
    background-color: black;
    margin: 1%;
    box-shadow: 0 3px 15px -2px;
    transition-duration: .1s;
}

.upButton:active{
    transform: scale(.9);
}

.up {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 14%;
    font-size: 38px;
    color: black;
    font-family: 'Londrina Outline', cursive;
    font-weight: 600;
    color: #ff4da6;
    transform: rotate(-15deg);
}
